@-webkit-keyframes slide-in {
from {
        -webkit-transform: translateY(-30px);
                transform: translateY(-30px);
        opacity: 0;
}
to {
        -webkit-transform: translateY(0);
                transform: translateY(0);
        opacity: 1;
}
}
@keyframes slide-in {
from {
        -webkit-transform: translateY(-30px);
                transform: translateY(-30px);
        opacity: 0;
}
to {
        -webkit-transform: translateY(0);
                transform: translateY(0);
        opacity: 1;
}
}
@-webkit-keyframes slide-out {
from {
        -webkit-transform: translateY(0);
                transform: translateY(0);
        opacity: 1;
}
to {
        -webkit-transform: translateY(-30px);
                transform: translateY(-30px);
        opacity: 0;
}
}
